import React, { Component } from "react";
import { Link } from "react-router-dom";
import Table from "./common/table";
import Like from "./common/like";

class MoviesTable extends Component {
  columns = [
    {
      path: "title",
      label: "Title",
      content: movie => <Link to={`/movies/${movie._id}`}>{movie.title}</Link>
    },
    { path: "genre.name", label: "Genre" },
    { path: "numberInStock", label: "Stock" },
    { path: "retailRate", label: "Rate" },
    { path: "wholeSaleRate", label: "Whole Sale Rate" },
    { path: "dpRate", label: "DP Rate" },
    {
      key: "like",
      content: movie => (
        <Like liked={movie.liked} onClick={() => this.props.onLike(movie)} />
      )
    },
    {
      key: "delete",
      content: movie => (
        <button
          onClick={() => this.props.onDelete(movie)}
          className="btn btn-danger btn-sm"
        >
          Delete
        </button>
      )
    }
  ];

  render() {
    const { movies, onSort, sortColumn, isRetail,isWholeSale } = this.props;
    
    if(!!isRetail)
    {
      this.columns = this.columns.filter(e=>e.path != 'delete')
      this.columns = this.columns.filter(e=>e.path != 'wholeSaleRate')
      this.columns = this.columns.filter(e=>e.path != 'dpRate')
      if(!!this.columns)
      this.columns[0].content=null;
    }

    if(!!isWholeSale)
    {
      this.columns = this.columns.filter(e=>e.path != 'delete')
      this.columns = this.columns.filter(e=>e.path != 'retailRate')
      this.columns = this.columns.filter(e=>e.path != 'dpRate')
      if(!!this.columns)
      this.columns[0].content=null;
    }

    return (
      <Table
        columns={this.columns}
        data={movies}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default MoviesTable;



